.map-wrapper {
    width: auto;
    height: auto;
    z-index: 1;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
}

.map-wrapper.open {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    z-index: 1000;
    transition: all 0.3s ease-in-out;
}

.map-wrapper .close-map-button {
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.map-wrapper.open .close-map-button {
    color: white;
    font-size: 1rem;
    position: absolute;
    top: 1rem;
    right: 1rem;
    opacity: 1;
}
.map-wrapper.open .close-map-button svg {
    color: transparent;
    stroke-width: 20;
    stroke: white;
    transition: all 0.3s ease-in-out;
}
.map-wrapper.open .close-map-button:hover svg {
    color: white;
    stroke-width: 20;
    stroke: rgb(77, 77, 77);
    transition: all 0.3s ease-in-out;
}

.map-wrapper .close-map-button svg {
    padding: .3rem;
    font-size: 2.3rem;
}

@media screen and (max-width: 30rem) {
    .map-wrapper.open .close-map-button {
        color: white;
        font-size: 1rem;
        position: fixed;
        top: 1rem;
        right: 33%;
        transform: translate(50%, 0);
    }
    .map-wrapper.open {
        margin-top: 5rem;
    }
}